// @mui
import { Container, Typography, Button } from '@mui/material';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { useSnackbar } from 'notistack';
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
import Iconify from '../../components/Iconify';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import useAuth from '../../hooks/useAuth';
import NetworkID from '../docs/network';
import DataID from '../docs/dataplan';
import CableID from '../docs/cableid';
import CablePlan from '../docs/cableplan';
import ExamID from '../docs/examid';
import DiscoID from '../docs/billid';

export default function Pricing() {
  const { themeStretch } = useSettings();
  const { user } = useAuth();
  const { enqueueSnackbar } = useSnackbar();
  return (
    <Page title="Pricingr">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={'Pricing'}
          links={[{ name: 'Dashboard', href: PATH_DASHBOARD.root }, { name: 'adex-system' }]}
        />
        <Typography variant="overline" sx={{ mb: 3, display: 'block', color: 'text.secondary' }}>
          Token: {user?.apikey}{' '}
          <CopyToClipboard text={user?.apikey} onCopy={() => enqueueSnackbar('Api Token')}>
            <Button title="copy">
              <Iconify icon="codicon:copy" sx={{ width: 20, height: 20, color: 'success.main' }} />
            </Button>
          </CopyToClipboard>
        </Typography>
        <NetworkID />
        <DataID />
        <CableID />
        <CablePlan />
        <ExamID />
        <DiscoID />
      </Container>
    </Page>
  );
}
